@import "sass/index";

.root {
  position: absolute;
  bottom: 0;
  z-index: 0;
  width: 100%;
  pointer-events: none;

  @media (max-width: $mobileScreen) {
    padding: 0 22px;
  }
}

.img {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100vw;
  height: auto;
  z-index: -1;
  margin: 0;

  @media (max-width: 1300px) {
    margin-bottom: 200px;
  }

  @media (max-width: 800px) {
    margin-bottom: 300px;
  }

  @media (max-width: $mobileScreen) {
    margin-bottom: 720px;
  }

  @media (max-width: $smallScreen) {
    display: none;
  }
}
